let data = [] // 用于接受生成数据的数组
for (let i = 0; i < 3; i ++) { // 可自定义生成的个数
    let arr = [];
    let template = {
        'name': '水果'+(i+1)+'号Pizza', // 可以生成基本数据类型
        'desciption': Random.cparagraph(1, 2), // 生成1到100之间自然数
        'options': arr, //3000        
    }    
    for( let j=0 ; j<2; j++){
        let temp = {
            'size': Random.integer(1, 50),
            'price': Random.integer(1, 100),
        }
        arr.push(temp)
    }
    data.push(template)
}
// 输出结果
