<template>
    <div class="row">
        <!-- 菜单 -->
        <div class="col-sm-12 col-md-8">
            <table class="table">
                <thead class="thead-default">
                    <tr>
                        <th>尺寸</th>
                        <th>价格</th>
                        <th>加入</th>
                    </tr>
                </thead>  

                <tbody
                    v-for="(item, index) in getMenuItems"
                    :item="item"
                    :index="index"
                    :key="item.name"
                >
                    <!-- <tbody v-for="item in getMenuItems" :key="item.name"> -->
                    <tr>
                        <td>
                            <strong>{{ item.name }}</strong>
                        </td>
                    </tr>
                    <tr v-for="option in item.options" :key="option.size">
                        <td>{{ option.size }}</td>
                        <td>{{ option.price }}</td>
                        <td>
                            <button
                                @click="addToBasket(item, option)"
                                class="btn btn-sm btn-outline-success"
                            >
                                加入
                            </button>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
        <!-- 购物车 -->
        <div class="col-sm-12 col-md-4">
            <p v-if="baskets.length==0">{{basketText}}</p>
            <div v-else  class="cart" >
                <table class="table">
                    <thead class="thead-default">
                        <tr>
                            <td>数量</td>
                            <td>品牌</td>
                            <td>价格</td>
                        </tr>
                    </thead>
                    <tbody v-for="item in baskets" :key="item.name">
                        <tr>
                            <td>
                                <button 
                                @click="decreaseQuantity(item)"
                                class="btn btn-sm" >-</button>
                                <span>{{item.quantity}}</span>
                                <button
                                @click="increaseQuantity(item)"
                                class="btn btn-sm">+</button>
                            </td>
                            <td>{{item.name}}-{{item.size}}</td>
                            <td>{{item.price * item.quantity}}</td>
                        </tr>
                    </tbody>
                </table>
                <p>总价：{{total}}RMB</p>
                <button class="btn btn-success btn-block">提交</button>
            </div>
        </div>
     
    </div>
</template>

<script>
import mock from "../mock/mock"
import axios from 'axios'
import { constants } from 'fs';

export default {
    data() {
        return {
            list: [],
            baskets: [],
            basketText: '购物车没有任何商品',
            list2: {
                1: {
                    name: "榴莲Pizza",
                    desciption: "芝士杀手--",
                    options: [{ size: 9, price: 36 }, { size: 12, price: 8 }]
                },
                2: {
                    name: "菠萝Pizza",
                    desciption: "菠萝杀手--",
                    options: [{ size: 9, price: 36 }, { size: 12, price: 8 }]
                },
                3: {
                    name: "鸡腿Pizza",
                    desciption: "鸡腿杀手--",
                    options: [{ size: 19, price: 36 }, { size: 102, price: 8 }]
                }
            }
        };
    },
    created() {
        // this.list2 = mock.rData;
        // console.log(this.list2);
        this.menuItems()
        
    },
    methods: {
        menuItems(){        
            // 请求数据 axios
            // this.http.post('url')
            // .then( res => {
            //     this.list2 = res.data   
            //     存到store
            //     this.$store.commit('setMenuItems',res.data)               
            // })            
             this.$store.commit('setMenuItems',this.list2)
             console.log(this.list2)
        },
        addToBasket(item, option) {
            let basket = {
                name: item.name,
                size: option.size,
                price: option.price,
                quantity: 1
            }
            if(this.baskets.length>0){
                //过滤
                let result =  this.baskets.filter(item => {                 
                    return item.name == basket.name && item.price == basket.price
                })
                console.log(result)
                if (result!=null && result.length>0) {
                  result[0].quantity++
                }else{
                    this.baskets.push(basket);
                }
            }else{
                this.baskets.push(basket);
            }            
        },
        decreaseQuantity(item){            
            item.quantity--
            if(item.quantity <= 0){
                this.removeFromBaset(item)
            }
        },
        increaseQuantity(item){
            item.quantity++
        },
        removeFromBaset(item){           
            this.baskets.splice(this.baskets.indexOf(item),1);           
        }
    },
    computed:{
        total(){
            let count = 0;
            for(let i in this.baskets){              
                count += this.baskets[i].price * this.baskets[i].quantity;
            }
          
            return count;
        },
        getMenuItems(){
            console.log(this.$store.state.menuItems)
            return this.$store.state.menuItems;
        }
    }
};
</script>


