<template>
    <div class="row mt-3">
        <div class="col-md-12 col-lg-12">
            <div class="card">
                <div class="card-body">
                    <img
                        class="mx-auto d-block"
                        src="../assets/logo.png"
                        alt=""
                    />
                    <form
                        @submit.prevent="onSubmit"
                        action=""
                        class=""
                    >
                        <div class="from-group">
                            <label for="email">邮箱</label>
                            <input
                                type="email"
                                class="form-control"
                                v-model="email"
                            />
                        </div>
                        <div class="from-group">
                            <label for="password">密码</label>
                            <input
                                type="password"
                                class="form-control"
                                v-model="password"
                            />
                        </div>
                        <div class="from-group mt-3">
                            <button class="btn btn-block btn-success ">
                                登陆
                            </button>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from "axios";
import { constants } from 'fs';
export default {
    data() {
        return {
            email: "41805222@qq.com",
            password: ""
        };
    },
    methods: {
        onSubmit() {
            axios.get('/user.json')
            .then(res=>{
                const users = []

                if(true){
                    let user = {    // 后台传来
                        email: '41805222@qq.com',
                        password: '111'
                    }
                    users.push(user)
                    console.log(users)

                    // 实现过滤
                    let result = users.filter( user => {
                        return user.email == this.email && user.password == this.password
                    })

                    // console.log(result)
                    // 判断result的长度是否大于0
                    if(result != null && result.length > 0){
                        this.$router.push({name: 'homeLink'})   // 跳首页
                    }else{  
                        alert('帐号或密码错误')
                    }
                }

            })
        }
    },
   
};
</script>