<template>
    <div class="row mt-3">
        <div class="col-md-12 col-lg-12">
            <div class="card">
                <div class="card-body">
                    <img class="mx-auto d-block" src="../assets/logo.png" alt />
                    <form @submit.prevent="onSubmit" action class>
                        <div class="from-group">
                            <label for="email">邮箱</label>
                            <input
                                type="email"
                                class="form-control"
                                v-model="email"
                            />
                        </div>
                        <div class="from-group">
                            <label for="password">密码</label>
                            <input
                                type="password"
                                class="form-control"
                                v-model="password"
                            />
                        </div>
                        <div class="from-group">
                            <label for="confirmpPassword">确认密码</label>
                            <input
                                type="password"
                                class="form-control"
                                v-model="confirmpPassword"
                            />
                        </div>
                        <div class="from-group mt-3">
                            <button class="btn btn-block btn-success">
                                注册
                            </button>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from "axios";

export default {
    data() {
        return {
            email: "41805222@qq.com",
            password: "",
            comfirmPassword: ""
        };
    },
    methods: {
        onSubmit() {
            if (this.password == this.confirmpPassword) {
                const formatData = {
                    email: this.email,
                    password: this.password,
                    confirmpPassword: this.confirmpPassword
                };
                axios.post("/users.json", formatData).then(res =>
                    this.$router.push({
                        name: "loginLink"
                    })
                );
            } else {
                alert("两次密码不一致");
                console.log(this.password);
                console.log(this.confirmpPassword);
                console.log(this.password == this.confirmpPassword);
            }
        }
    }
};
</script>